import React from 'react'

class AudioCompo extends React.Component{
    constructor(props){
        super(props);
    }
    render(){
        return(
            <audio id={this.props.Audioid} className="clip" src={this.props.Audiosrc} type="audio/mp3">Your browser does not support the <code>audio</code> element.</audio>
        )
    }
}

export default AudioCompo