import React from 'react'
import AudioCompo from './audioCompo'

class DrumMachine extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            display: "",
        }
        this.handleClick = this.handleClick.bind(this)
        this.handleKeyPress = this.handleKeyPress.bind(this)
    }

    handleClick(event){
        var inner = event.target.innerText
        this.setState({
            display: event.target.innerText
        })
        document.getElementById(inner).play()
    }


    handleKeyPress(event){
        if(event.keyCode === 81 | event.keyCode === 87 | event.keyCode === 69 | event.keyCode === 65 | event.keyCode === 83 | event.keyCode === 68 | event.keyCode === 90 | event.keyCode === 88 | event.keyCode === 67){
            this.setState({
                display: String.fromCharCode(event.keyCode)
            })
            document.getElementById(String.fromCharCode(event.keyCode)).play()
        }
    }

    componentDidMount(){
        document.addEventListener("keydown", this.handleKeyPress)
    }
    componentWillUnmount() {
        document.removeEventListener("keydown", this.handleKeyPress);
    }

    render(){
        return(
            <div id="drum-machine">
                <div id="displaydiv">
                    <h1 id="display">{this.state.display}</h1>
                </div>
                <div id="pads">
                    <div id="1" onClick={this.handleClick} className="drum-pad">
                        <AudioCompo Audioid="Q" Audiosrc="https://s3.amazonaws.com/freecodecamp/drums/Heater-1.mp3"/>
                    Q</div>
                    <div id="2" onClick={this.handleClick} className="drum-pad">
                        <AudioCompo Audioid="W" Audiosrc="https://s3.amazonaws.com/freecodecamp/drums/Heater-2.mp3"/>
                    W</div>
                    <div id="3" onClick={this.handleClick} className="drum-pad">
                        <AudioCompo Audioid="E" Audiosrc="https://s3.amazonaws.com/freecodecamp/drums/Heater-3.mp3"/>
                    E</div>
                    <div id="4" onClick={this.handleClick} className="drum-pad">
                        <AudioCompo Audioid="A" Audiosrc="https://s3.amazonaws.com/freecodecamp/drums/Heater-4_1.mp3"/>
                    A</div>
                    <div id="5" onClick={this.handleClick} className="drum-pad">
                        <AudioCompo Audioid="S" Audiosrc="https://s3.amazonaws.com/freecodecamp/drums/Heater-6.mp3"/>
                    S</div>
                    <div id="6" onClick={this.handleClick} className="drum-pad">
                        <AudioCompo Audioid="D" Audiosrc="https://s3.amazonaws.com/freecodecamp/drums/Dsc_Oh.mp3"/>
                    D</div>
                    <div id="7" onClick={this.handleClick} className="drum-pad">
                        <AudioCompo Audioid="Z" Audiosrc="https://s3.amazonaws.com/freecodecamp/drums/Kick_n_Hat.mp3"/>
                    Z</div>
                    <div id="8" onClick={this.handleClick} className="drum-pad">
                        <AudioCompo Audioid="X" Audiosrc="https://s3.amazonaws.com/freecodecamp/drums/Heater-3.mp3"/>
                    X</div>
                    <div id="9" onClick={this.handleClick} className="drum-pad">
                        <AudioCompo Audioid="C" Audiosrc="https://s3.amazonaws.com/freecodecamp/drums/RP4_KICK_1.mp3"/>
                    C</div>
                </div>
            </div>
        )
    }
}

export default DrumMachine